<template>
	<div>
		<w-cell>
			<span slot="cell-title">
				(menus):{{value?'':''}}
			</span>
			<span slot="cell-value">
				<w-switch v-model="value"></w-switch>
			</span>
		</w-cell>
		<w-cell>
			<span slot="cell-title">
				(menus):{{value2?'':''}}
			</span>
			<span slot="cell-value">
				<w-switch v-model="value2"></w-switch>
			</span>
		</w-cell>
		<w-actionSheet v-model="value" title="" :menus="menus" :showCancel="true" v-on:on-cancel="onCancel" :clickMaskHide="true" v-on:on-menu-confirm="onConfirm" v-on:on-menu="onNoValue" @on-mask='onMask'></w-actionsheet>
		<w-actionSheet v-model="value2" title="" :menus="menus2" :showCancel="true" v-on:on-menu-confirm="onConfirm" v-on:on-cancel="onCancel" :clickMaskHide="false" ></w-actionsheet>

	</div>
</template>

<script>

export default {
	name: 'actionSheetDemo',
	data () {
		return {
			value: false,
			value2: false,
			menus: [
				{
					label: '',
  					type: 'primary',
					value: 'confirm'
				},
				{
					label: 'warning',
  					type: 'warning',
					value: 'warning'
				},
				{
					label: 'disabled',
  					type: 'disabled',
					value: 'disabled'
				},
				{
					label: '<span style="color:blue;">Default+</span>'
				},
				{
					label: '',
  					type: 'primary',
					clickMenuHide: false
				}
			],
			menus2: {
				label: '',
				type: 'primary',
				value: 'confirm'
			}
		}
	},
	created () {
	},
	methods: {
		onConfirm () {
			console.log('confirm')
		},
		onCancel () {
			console.log('cancel')
		},
		onMask () {
			console.log('onMask')
		},
		onNoValue () {
			console.log('value')
		}
	}
}
</script>

<style>

</style>



