<template>
	<div>
		<div>,</div>
		<w-picker :data="pickerData0" v-model="pickerValue0" :rowCount='5' ref="picker0"></w-picker>
		<w-cell title=":">{{$refs.picker0 && $refs.picker0.getNameValues()}}</w-cell>
		<w-button type='primary' @click="setPickerValue('pickerValue0',[''])"></w-button>
		<div>,</div>
		<w-picker :data="pickerData1" v-model="pickerValue1" :rowCount='5'></w-picker>
		<w-cell title=":">{{pickerValue1}}</w-cell>
		<div></div>
		<w-picker :data="pickerData3" v-model="pickerValue" :rowCount='5' ref="picker1" @on-change='onChange' @on-value-change='onValueChange' :columnWidthFlex="[2,1]" :linkage="true"></w-picker>
		<w-cell title=":">{{$refs.picker1 && $refs.picker1.getNameValues()}}</w-cell>
		<w-cell title="value:">{{pickerValue}}</w-cell>
		<w-button type='primary' @click="setPickerValue('pickerValue',['3','23'])">,</w-button>
		<w-button type='primary' @click="comfirm"></w-button>
	</div>
</template>

<script>

export default {
	name: 'PickerDemo',
	data () {
		return {
			pickerData0: ['', '', '', '', '', '', '', ''],
			pickerValue0: [],
			pickerData1: [
				['', '', '', '', '', '', ''],
				['', '', '', '', '', '', ''],
				['', '', '', '', '', '', '']
			],
			pickerValue1: [],
			pickerData3: [
				[{
					name: '',
					value: '1'
				},
				{
					name: '',
					value: '2'
				},
				{
					name: '',
					value: '3'
				},
				{
					name: '',
					value: '4'
				},
				{
					name: '',
					value: '5'
				},
				{
					name: '',
					value: '6'
				},
				{
					name: '',
					value: '7'
				}],
				[{
					name: '',
					value: '11',
					parent: '1'
				},
				{
					name: '',
					value: '12',
					parent: '2'
				},
				{
					name: '',
					value: '13',
					parent: '3'
				},
				{
					name: '',
					value: '23',
					parent: '3'
				},
				{
					name: '',
					value: '14',
					parent: '4'
				},
				{
					name: '',
					value: '24',
					parent: '4'
				},
				{
					name: '',
					value: '15',
					parent: '5'
				},
				{
					name: '',
					value: '25',
					parent: '5'
				},
				{
					name: '',
					value: '16',
					parent: '6'
				},
				{
					name: '',
					value: '17',
					parent: '7'
				}
				]
			],
			pickerValue: [],
			pickerName: []
		}
	},
	created () {
	},
	mounted () {
		this.pickerName = this.$refs.picker1.getNameValues()
	},
	watch: {
		//  pickerName,  {{$refs.picker1 && $refs.picker1.getNameValues()}} 
		// pickerValue () {
		// 	console.log('pickervalue change')
		// 	this.pickerName = this.$refs.picker1.getNameValues()
		// }
	},
	methods: {
		setPickerValue (oldPickerValue, pickerValue) {
			this[oldPickerValue] = pickerValue
		},
		onChange (val) {
			console.log('change:')
			console.log(val)
		},
		onValueChange (val) {
			console.log(`value[${val}]`)
		},
		comfirm () {
			let valueName = this.$refs.picker1.getNameValues()
			this.$WToast({
				message: `${valueName},value${this.pickerValue}`,
				maskShow: false,
				type: 'text',
				width: '300'
			})
		}
	}
}
</script>

<style>

</style>



