<template>
	<div>
		<div class="button-group-title"></div>
		<div class="button-group">
			<w-button type='primary' @click="showToast1">Top</w-button>
			<w-button type='primary' @click="showToast2">Bottom</w-button>
			<w-button type='primary' @click="showToast3">Middle</w-button>
		</div>
		<w-cell>
			<span slot="cell-title">
				Default:{{value8?'':''}}
			</span>
			<span slot="cell-value">
				<w-switch v-model="value8" @change="showToast4"></w-switch>
			</span>
		</w-cell>
		<w-cell>
			<span slot="cell-title">
				:{{value?'':''}}
			</span>
			<span slot="cell-value">
				<w-switch v-model="value"></w-switch>
			</span>
		</w-cell>
		<w-cell>
			<span slot="cell-title">
				:{{value1?'':''}}
			</span>
			<span slot="cell-value">
				<w-switch v-model="value1"></w-switch>
			</span>
		</w-cell>
		<w-cell>
			<span slot="cell-title">
				typetext:{{value2?'':''}}
			</span>
			<span slot="cell-value">
				<w-switch v-model="value2"></w-switch>
			</span>
		</w-cell>
		<w-cell>
			<span slot="cell-title">
				typecancel:{{value3?'':''}}
			</span>
			<span slot="cell-value">
				<w-switch v-model="value3"></w-switch>
			</span>
		</w-cell>
		<w-cell>
			<span slot="cell-title">
				typewarning:{{value4?'':''}}
			</span>
			<span slot="cell-value">
				<w-switch v-model="value4"></w-switch>
			</span>
		</w-cell>
		<w-cell>
			<span slot="cell-title">
				1:{{value5?'':''}}
			</span>
			<span slot="cell-value">
				<w-switch v-model="value5"></w-switch>
			</span>
		</w-cell>
		<w-cell>
			<span slot="cell-title">
				:{{value6?'':''}}
			</span>
			<span slot="cell-value">
				<w-switch v-model="value6"></w-switch>
			</span>
		</w-cell>
		<w-cell>
			<span slot="cell-title">
				:{{value7?'':''}}
			</span>
			<span slot="cell-value">
				<w-switch v-model="value7"></w-switch>
			</span>
		</w-cell>
		<w-toast v-model="value" message="" @on-show='onShow' @on-hide='onHide'></w-toast>
		<w-toast v-model="value1" message="" :mask-show='true' @on-show='onShow' @on-hide='onHide'></w-toast>
		<w-toast v-model="value2" message="" position="middle" type="text" @on-show='onShow' @on-hide='onHide'></w-toast>
		<w-toast v-model="value3" message="typecancel" position="middle" type="cancel" @on-show='onShow' @on-hide='onHide'></w-toast>
		<w-toast v-model="value4" message="type" position="middle" type="warning" @on-show='onShow' @on-hide='onHide'></w-toast>
		<w-toast v-model="value5" message="1" position="middle" type="success" :duration="1000" @on-show='onShow' @on-hide='onHide'></w-toast>
		<w-toast v-model="value6" message="" position="middle" type="text" :width='300' @on-show='onShow' @on-hide='onHide'></w-toast>
		<w-toast v-model="value7" message="" position="middle" iconClass="github-icon" @on-show='onShow' @on-hide='onHide'></w-toast>
	</div>
</template>

<script>

export default {
	name: 'dialogDemo',
	data () {
		return {
			value: false,
			value1: false,
			value2: false,
			value3: false,
			value4: false,
			value5: false,
			value6: false,
			value7: false,
			value8: false

		}
	},
	created () {
	},
	methods: {
		onShow () {
			console.log('show')
		},
		onHide () {
			console.log('hide')
		},
		showToast1 () {
			this.$WToast({
				message: 'top',
				position: 'top',
				type: 'text',
				duration: 1000,
				width: '200px'
			})
		},
		showToast2 () {
			this.$WToast({
				message: 'bottom',
				position: 'bottom',
				type: 'text',
				duration: 1000
			})
		},
		showToast3 () {
			this.$WToast({
				message: 'middle',
				position: 'middle',
				type: 'text',
				duration: 1000
			})
		},
		showToast4 () {
			if (!this.value8) {
				return false
			}
			this.$WToast({
				message: 'default',
				type: 'text',
				duration: 1000
			}).then(() => {
				this.value8 = false
			})
		}
	}
}
</script>

<style>
	.button-group {
		padding-left:10px;
		padding-right:10px;
		margin-top:10px;
		margin-bottom:10px;
	}
	.button-group-title {
		text-align: left;
		padding-left:10px;
		padding-right:10px;
	}
	.github-icon {
		background: url('../assets/github-white.png') no-repeat center center;
	}
</style>



