import WSwitch from './packages/switch' //switch 
import WLoading from './packages/loading/Loading.vue' //loading 
import WLoadingType from './packages/loadingType' //loadingType 
import WToast from './packages/toast/Toast.vue' //toast 
import WDialog from './packages/w-dialog' //dialog 
import WConfirm from './packages/confirm/Confirm.vue' //confirm 
import WAlert from './packages/alert/Alert.vue' //alert 
import WActionSheet from './packages/actionsheet' //actionSheet 
import WPopup from './packages/popup' //popup 
import WButton from './packages/button' //button 
import WHeader from './packages/header' //button 
import WCell from './packages/cell' //cell 
import WPicker from './packages/picker' //picker 
import WCheckList from './packages/checkList' //checkList 
import {Tabbar, TabbarItem} from './packages/tabbar' //tabbar 
import {IndexList, IndexListItem} from './packages/indexList' //tabbar 
import {Swipe, SwipeItem} from './packages/swipe' //swipe 

import WLoadingPlugin from './packages/loading' //loading (plugin)
import WToastPlugin from './packages/toast' //toast (plugin)
import WConfirmPlugin from './packages/confirm' //confirm (plugin)
import WAlertPlugin from './packages/alert' //alert (plugin)

import './src/style/style.css'

const WindUI = {
	install: function (Vue) {
                Vue.component('w-switch', WSwitch)
                Vue.component('w-loading', WLoading)
                Vue.component('w-loadingType', WLoadingType)
                Vue.component('w-toast', WToast)
                Vue.component('w-dialog', WDialog)
                Vue.component('w-confirm', WConfirm)
                Vue.component('w-alert', WAlert)
                Vue.component('w-actionSheet', WActionSheet)
                Vue.component('w-popup', WPopup)
                Vue.component('w-button', WButton)
                Vue.component('w-tabbar', Tabbar)
                Vue.component('w-tabbar-item', TabbarItem)
                Vue.component('w-header', WHeader)
		Vue.component('w-cell', WCell)
                Vue.component('w-indexList', IndexList)
                Vue.component('w-indexList-item', IndexListItem)
                Vue.component('w-swipe', Swipe)
                Vue.component('w-swipe-item', SwipeItem)
                Vue.component('w-picker', WPicker)
                Vue.component('w-checkList', WCheckList)
        
                // pluginUI
                Vue.use(WLoadingPlugin) 
                Vue.use(WToastPlugin)
                Vue.use(WConfirmPlugin)
                Vue.use(WAlertPlugin)
	}
}

export default WindUI



